body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar{
    background-color: rgb(76,55,158);
    position: fixed;
    width: 100%;
}

.guess{
    font-size: 30px;
}

#Right{
    color: rgb(85, 223, 85);
}

#Wrong{
    color: red;
}
.mainTitle, .lead{
    text-align: center;
}

.container{
    padding-top: 80px;
}

.card{
  height: 160px;
  width: 160px;
  /* padding: 10px; */
  float: left;
  margin: 20px;
}

img{
  width: 150px;
  height: 150px;
}
.wrapper {
  max-width: 1000px;
  margin: auto;
  padding-top: 50px;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}
  

